html,
body {
    min-height: 100vh;
    font-size: 17px;
    color: $color-black-text;
}

body {
    display: flex;
    flex-direction: column;

    header {

        @include shadow();
        z-index: 3;
        position: sticky;
        top:0;
        width:100%;
        &.unauthorized {
            background-color: $color-white;
        }


    }

    main {
        flex-grow: 1;
        background-color: $color-white;


    }

    footer {
        flex: 0 0 auto;
        width: 100%;
        min-height: 320px;

        background: url('../../assets/img/fon_footer.jpg') no-repeat center / cover;
        position: relative;
        z-index: 1;

        &::after {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 137, 141, 0.8);
        }
    }
}
